import React, { Component } from 'react';
import { Chart } from 'primereact/components/chart/Chart';
class AuditReport extends Component {

  render() {
    var data = {
     
      datasets: [
          {
              data: [300],
              backgroundColor: [
                  "rgb(146, 103, 177)"
              ],
              hoverBackgroundColor: [
                  "rgb(146, 103, 177)"
              ]
          }]    
      };
    return (

      <div class="container-fluid">
      
<br/>      
        <div class="card"  >
          <div class="card-body" style={{ paddingTop: '0', paddingLeft: '15px', paddingRight: '15px' }}>
            <div class="row" style={{ backgroundColor: '#F5F5F5', paddingTop: '12px' }}>
              <div class="col-lg-4">
                <div class="form-group row">
                  <label class="col-4 col-form-label" for="startdate"> Select Date:</label>
                  <div class="col-8">
                    <input class="form-control" type="date" id="select-date-input" />
                  </div>
                </div>
              </div>
              <div class="col-lg-1">
                <a id="showLink" class="btn btn-primary" style={{ backgroundColor: '#337ab7', color: 'white' }}>Submit</a>
              </div>
            </div>
            <br />
            <div class="row"  style={{lineHeight:1.5}}>
              <div class="col-lg-7">
                <div id="stateList">
                  <ul class="nav nav-pills nav-justified" role="tablist">
                    <li class="nav-item" stateCode="IL">
                      <a class="nav-link border active" data-toggle="tab" href="#illinois">Illinois</a>
                    </li>
                    <li class="nav-item"  stateCode="OK">
                      <a class="nav-link border" data-toggle="tab" href="#menu1">Oklahoma</a>
                    </li>
                    <li class="nav-item" stateCode="NM">
                      <a class="nav-link border" data-toggle="tab" href="#menu2">New Mexico</a>
                    </li>
                    <li class="nav-item"  stateCode="TX">
                      <a class="nav-link border" data-toggle="tab" href="#menu1">Texas</a>
                    </li>
                    <li class="nav-item" stateCode="MT">
                      <a class="nav-link border" data-toggle="tab" href="#menu2">Montana</a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <br />
            <div class="row data">
                    <div class="col-xl-4 col-lg-4">
                        <div>
                            <ul class="list-group">
                                <li class="list-group-item">
                                    <div class="row">

                                        <div class="col-xl-6">
                                            <label class="float-left">Total members </label>
                                        </div>
                                        <div class="col-xl-1">:</div>
                                        <div class="col-xl-5">
                                            <span id="TotalMembers"></span>
                                        </div>

                                    </div>
                                </li>
                                <li class="list-group-item">
                                    <div class="row">

                                        <div class="col-xl-6">
                                            <label class="float-left">Good members </label>
                                        </div>
                                        <div class="col-xl-1">:</div>
                                        <div class="col-xl-4">
                                            <span id="GoodMembers"></span>
                                        </div>

                                    </div>

                                </li>
                                <li class="list-group-item">
                                    <div class="row">

                                        <div class="col-xl-6">
                                            <label class="float-left">Good members % </label>
                                        </div>
                                        <div class="col-xl-1">:</div>
                                        <div class="col-xl-5">
                                            <span id="GoodMembersPercent"></span>
                                        </div>

                                    </div>

                                </li>
                                <li class="list-group-item">
                                    <div class="row">

                                        <div class="col-xl-6">
                                            <label class="float-left">Audit members </label>
                                        </div>
                                        <div class="col-xl-1">:</div>
                                        <div class="col-xl-5">
                                            <span id="AuditMembers"></span>
                                        </div>

                                    </div>

                                </li>
                                <li class="list-group-item">
                                    <div class="row">

                                        <div class="col-xl-6">
                                            <label class="float-left">Audit Good members </label>
                                        </div>
                                        <div class="col-xl-1">:</div>
                                        <div class="col-xl-5">
                                            <span id="AuditGoodMembers"></span>
                                        </div>

                                    </div>

                                </li>
                                <li class="list-group-item">
                                    <div class="row">

                                        <div class="col-xl-6">
                                            <label class="float-left">Audit Good members % </label>
                                        </div>
                                        <div class="col-xl-1">:</div>
                                        <div class="col-xl-5">
                                            <span id="AuditGoodMembersPercent"></span>
                                        </div>

                                    </div>

                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-xl-2 col-lg-2">
                        <label>Missing In DP</label>
                        <button type="button" class="btn btn-light btn-sm" >
                            <span class="fa fa-list-alt"></span>
                        </button>
                        <Chart type="pie" data={data} />
                    </div>
                    <div class="col-xs-2 col-lg-2">
                        <label>PTD Mismatch </label>
                        <button type="button" class="btn btn-light btn-sm" >
                            <span class="fa fa-list-alt"></span>
                        </button>
                        <Chart type="pie" data={data} />
                    </div>
                    <div class="col-xs-2 col-lg-2">
                        <label>Missing In RMS </label>
                        <button type="button" class="btn btn-light btn-sm" >
                            <span class="fa fa-list-alt"></span>
                        </button>
                        <Chart type="pie" data={data} />
                    </div>
                    <div class="col-xs-2 col-lg-2">
                        <label>Other Members</label>
                        <button type="button" class="btn btn-light btn-sm" >
                            <span class="fa fa-list-alt"></span>
                        </button>
                        
                        <Chart type="pie" data={data} />
                    </div>
                </div>
          </div>
        </div>
      </div>


    );
  }
}

export default AuditReport;


